<template>
  <div>
    <div v-show="showPic" @click="animate() ">
      <b-icon-arrow-left class="back-btn"></b-icon-arrow-left>
      <span class="heading">CORONA TRACKER</span>
    </div>
    <div :class="{'wrapper-off':toggle ,'wrapper-on':!toggle }">
      <img class="home-img" src="@/assets/home.png" />
      <b-btn variant="danger" @click="animate()" class="home-btn">TRACK CORONA</b-btn>
    </div>
    <div v-if="toggle" class="search-page">
      <div>
        <img class="search-img" src="@/assets/search.png" />
        <input
          class="search-bar p-2"
          maxlength="6"
          placeholder="ENTER YOUR PIN-CODE"
          v-model="zip"
          type="number"
        />
        <b-icon-search @click="getTotalCount()" class="search-lens"></b-icon-search>
      </div>
      <img v-if="loader" style="width:6rem;display:block;margin:10rem auto;" src="@/assets/loader.svg"/>
      <b-alert style="margin-top:5rem;" v-if="inputError == true " show variant="danger">Invalid pin code</b-alert>
      <div v-if="inputError == false && gotResult==true">
        <div class="total-number mb-3">
          <img class="virus" src="@/assets/danger.svg" />
          {{totalNumber}} People
        </div>
        <div class="people-details">
          This tells the number of quarantined in your Area/PinCode. The data shown here is the same given by government of Karnataka Website.
          <!-- <p>
            <b-icon-building class="people-icon"></b-icon-building>STREET:
            <span class="people-data">from italy</span>
          </p>
          <p>
            <b-icon-geo-alt class="people-icon"></b-icon-geo-alt>TRAVEL-HISTORY:
            <span class="people-data">bharti street</span>
          </p>
          <p>
            <b-icon-watch class="people-icon"></b-icon-watch>QUARTINED TILL:
            <span class="people-data">2nd march</span>
          </p> -->
        </div>
        <img style="width:95%; border-radius:9px; margin-top:10px;" src="@/assets/tenor.gif" />
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      zip: "",
      inputError: false,
      gotResult:false,
      loader:false,
      toggle: false,
      showPic: true,
      totalNumber: 0
    };
  },
  methods: {
    animate() {
      this.toggle = !this.toggle;
      this.showPic = this.toggle;
    },
    getDetails(){
       var zip = this.zip;
           var url =
        "https://mcbc3n72td.execute-api.ap-south-1.amazonaws.com/dev/items/" +
         zip;
      axios.get(url).then(response => {
          this.loader = false;
        console.log(response.data,"people details");
      }); 
    },
    getTotalCount() {
      this.loader = true;
      var zip = this.zip;
       this.gotResult = false;
      if(zip.length == 6){
          var url =
        "https://mcbc3n72td.execute-api.ap-south-1.amazonaws.com/dev/items/count/" +
        zip;
      axios.get(url).then(response => {
        this.loader = false;
        this.inputError = false;
          this.gotResult = true;
        console.log(response.data);
        this.totalNumber = response.data.result;
        this.getDetails()
      });
      }else{
        this.inputError = true;
        this.loader=false;
      }
    }
  },
  mounted() {}
};
</script>
<style scoped>
.people-data {
  font-weight: 800;
  text-align: left;
}
.people-icon {
  color: #dc3545;
  margin: 0.2rem;
  font-size: 15px;
}
.people-details {
  height: auto;
  width: 95%;
  border: 1px solid #dc3545;
  padding: 1rem;
  margin: 5px 0 5px 0;
  border-radius: 7px;
}
.virus {
  width: 3rem;
  fill: #dc3545 !important;
}
.total-number {
  color: #dc3545;
  margin-top: 6rem;
  height: 5rem;
  width: 95%;
  background-color: white;
  font-size: 30px;
  text-align: center;
  padding: 1rem;
}
.heading {
  position: absolute;
  top: 1rem;
  left: 7rem;
  font-size: 20px;
}
.search-img {
  width: 3rem;
  height: 3rem;
  float: left;
}
.search-bar {
  width: calc(100% - 4rem - 4rem);
  height: 3rem;
  border-radius: 6px;
  border: none;
  display: block;
  margin: 0 auto;
  float: left;
  margin-left: 10px;
  margin-right: 10px;
}
.search-lens {
  color: #dc3545;
  width: 3rem;
  height: 3rem;
  float: left;
}
.back-btn {
  position: absolute;
  top: 10px;
  left: 3px;
  font-size: 40px;
  padding-left: 1.5rem;
  border-radius: 6px;
  width: 5rem;
  background-color: #dc3545;
  color: white;
}
.home-img {
  width: 19rem;
  display: block;
  margin: 0 auto;
}
.home-btn {
  width: 15rem;
  margin: 0 auto;
  margin-top: 1rem;
  padding: 2rem;
  font-size: 25px;
  font-weight: 900;
  display: block;
}
.wrapper-on {
  height: 20rem;
  margin-top: 5rem;
}
.wrapper-off {
  height: 0 !important;
  transition: 1s height ease-in-out;
  overflow: hidden;
  margin-top: 5rem;
}
.search-page {
  display: block;
  padding: 1rem;
  min-height:80vh;
  width: 100%;
  background-color: #eeede8;
  overflow-y:hidden;
}
</style>
